<template>
  <div class="box">
    <div class="box_top">
      发布
    </div>
    <div>
      <van-cell-group class="name">
        <van-field v-model="title" placeholder="请输入标题" />
      </van-cell-group>
      <van-cell-group class="name">
        <van-field v-model="message" rows="2" autosize type="textarea" maxlength="500" placeholder="请输入留言"
          show-word-limit />
      </van-cell-group>
    </div>
    <div class="select">
      请选择发布的类型
      <select v-model="value">
        <option :value=item v-for="(item,i) in arr" :key="i">{{item.name}}</option>
      </select>
    
    </div>
    <!-- 上传图片 -->
    <div>
      <van-uploader v-model="fileList" multiple :after-read="afterRead" />
    </div>
    

    <div class="btn">
      <van-button type="default" block color="rgb(7,193,76)" @click="getfabu">发布</van-button>
    </div>

  </div>
</template>
 
<script>
import { Field,Cell, CellGroup,Button,Uploader,Toast    } from 'vant';
// import bus from '@/views/busEvent/bus.vue'
import { nanoid } from 'nanoid';
export default {
  name:'public',
  components:{
    [Field.name]:Field,
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup,
    [Button.name]:Button, 
    [Uploader.name]:Uploader ,
    [Toast.name]:Toast 
  },
  data() {
    return {
      value:'',
      message:'',
      title:'',
      arr:[],
      uid:'634fe8fb702e9d0001edf2de',
      username: "17613926309",
      fileList:[],
      files:null
    }
  },
  methods:{
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      this.files=file.file
      // console.log(this.fileList);
    },
    setfenlei() {
      this.$axios({
        method: 'get',
        url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list'
      }).then(res => {
        this.arr = res.data.data
        console.log(this.arr);
      })
    },
    getfabu(){
      // 获取发布图片的token
       this.$axios({
        method:'get',
        url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token',
       }).then(res=>{
          //  console.log(res.data.token); 
          let FD =new FormData();
             FD.append('key',nanoid());
             FD.append('file',this.files);
             FD.append('token',res.data.token);
          this.$axios({
            method:'post',
            url:'https://upload-z1.qiniup.com',
            data:FD
          }).then(res=>{
            // console.log(res);
            this.$axios({
                method: 'post',
                url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article',
                data:{
                    "title":this.title,
                    'content':this.message,
                    'cate_id':this.value._id,
                    'cate_name':this.value.name,
                    'author_id':localStorage.getItem('uid'),
                    'imageSrc':[`http://toutiao.longxiaokj.com/${res.data.key}`]
                }
              }).then(res => {
                // this.arr = res.data.data
                if(res.data.code===0){
                  this.title=''
                  this.message=''
                  this.value=''
                  Toast(res.data.msg);
                  this.fileList=[]
                }
                
                console.log(res);
              })
          })
       })
      //
      // console.log(this.fileList);
    }
  },
  created () {
    this.setfenlei();
    let token=localStorage.getItem('token')
    let uid =localStorage.getItem('uid')
    if (uid&&token) {
      
    }else{
      Toast('你还没登录，请先登录')
      this.$router.push({
        name:'login'
      })
    }
  }
}
</script>

<style lang="less" scoped>
.box{
  width: 100vw;
  height: 100vh;
  background-color: rgb(236, 236, 236);
  .box_top{
    width: 100%;
    height: 40px;
    text-align:center;
    line-height: 40px;
    color: #fff;
    background-color:rgb(7,193,76);
  }
  .name{
    margin-top: 10px;
  }
  .select{
    margin: 5px 10px;
  }
}
.btn{
  width: 80vw;
  margin: 20px auto;
}
</style>